import { Dispatch } from "react"
import { SetStateAction } from "jotai"
import { HtmlPageHead } from "@rp/react/web/common/header-components"
import { FilesUploadDropZone } from "@rp/react/web/files"
import { MyFileContainer } from "./Files.styles"
import { FilesList } from "./FilesList/FilesList"

export type SelectedFile = { id: string; shared: boolean }

type Props = {
  filterTerm: string
  disableUpload: boolean
  selected: Array<SelectedFile>
  setSelected: Dispatch<SetStateAction<Array<SelectedFile>>>
  shared?: boolean
}

const Files = ({ filterTerm, disableUpload, selected, setSelected, shared }: Props) => {
  return (
    <MyFileContainer>
      <HtmlPageHead title="Files" />
      <FilesUploadDropZone disabled={disableUpload}>
        <FilesList
          shared={shared}
          selected={selected}
          setSelected={setSelected}
          filterTerm={filterTerm}
        />
      </FilesUploadDropZone>
    </MyFileContainer>
  )
}

export default Files
